import { makeStyles } from '@material-ui/core';
import dashboardStyle from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import GridContainer from "components/Grid/GridContainer.js";
// core components
import GridItem from "components/Grid/GridItem.js";
import moment from "moment";
import React from 'react';
import ItemHourly from './ItemHourly';


const useStyles =makeStyles({
    cardHeaderStyle : {
        fontSize: 24,
        fontWeight : "bold"
    },
    iconWeather:{
        display:"flex",
    },
    formatInline:{
        display:"flex",
        alignItems:"center",
        justifyContent:"center"
    },
    scrollBar:{
        "&::-webkit-scrollbar-thumb":{
            borderRadius: 6,
            backgroundColor: "gray",

        },
        "&::-webkit-scrollbar":{
            width: 4,
            backgroundColor: "#f5f5f5",
            borderRadius: 6,
            height:8
        }
    },
    ...dashboardStyle
})

function CardHourlyInformation(props){
    const {color,
        date, //ngày
        iconCode, //icon thời tiết
        temp, //nhiệt độ
        speedWind, //tốc độ gió
        clouds, // mây
        humidity, // độ ẩm
        sunrise, // mặt trời mọc
        sunset,// mt lặn
        pressure, // áp suất kk
        max_temp, // nhiệt độ cao nhất,
        min_temp, // nhiệt độ thấp nhất
        description // mô tả
    }= props;
    const classes = useStyles();

    const GetIconWeather = (code)=>{
        return "http://openweathermap.org/img/wn/"+code+"@2x.png";
    }
    return (
        <Card>
            <CardHeader color={'rose'}>
                <div >
                    <h3 className={`${classes.formatInline} ${classes.cardTitleWhite}`}>
                        Thông tin thời tiết ngày &nbsp;
                        {moment(date).format("DD-MM-YYYY")}
                    </h3>
                </div>
            </CardHeader>
            <CardBody style={{ fontSize: "17px" }}>
                <GridContainer>
                    <GridItem xs={12} sm={4}>
                        <div style={{display: "flex", alignItems:"center", justifyContent:"center", marginBottom:"8px"}}>
                            <img src={GetIconWeather(iconCode)} alt="" style={{backgroundColor: "rgb(0,0,0,0.3)", borderRadius:"4px"}}/>
                        </div>
                            <div style={{fontSize:32, fontWeight: "bold", display: "flex", alignItems:"center", justifyContent:"center"}}>
                                {Math.round((temp - 273.15) * 10) / 10}°C
                            </div>
                    </GridItem>

                    <GridItem xs={12} sm={8}>
                        <GridContainer>
                            <GridItem xs={6}>
                                <div style={{fontSize: 14}}>
                                    <b>Nhiệt độ cao nhất :</b> {Math.round((max_temp - 273.15) * 10) / 10}°C
                                </div>
                                <div style={{fontSize: 14}}>
                                    <b>Nhiệt độ thấp nhất :</b> {Math.round((min_temp - 273.15) * 10) / 10}°C
                                </div>
                                <div style={{fontSize: 14}}>
                                    <b>Tốc độ gió :</b> {speedWind} m/s
                                </div>
                                <div style={{fontSize: 14}}>
                                    <b>Độ ẩm :</b> {humidity} %
                                </div>
                            </GridItem>
                            <GridItem xs={6}>
                                <div style={{fontSize: 14}}>
                                    <b>Mây :</b> {clouds}%
                                </div>
                                <div style={{fontSize: 14}}>
                                    <b>Mặt trời mọc :</b> {moment(sunrise).format("HH:mm:ss")}
                                </div>
                                <div style={{fontSize: 14}}>
                                    <b>Mặt trời lặn :</b> {moment(sunset).format("HH:mm:ss")}
                                </div>
                                <div style={{fontSize: 14}}>
                                    <b>Áp suất không khí :</b> {pressure} hPa
                                </div>
                            </GridItem>
                            <GridItem xs={12}>
                                <div style={{fontSize: 14}}>
                                    <b>Mô tả :</b> {description}
                                </div>
                            </GridItem>
                        </GridContainer>
                    </GridItem>
                </GridContainer>
                <div style={{marginTop:12, marginBottom:12}}>THỜI TIẾT 48 GIỜ TỚI</div>
                <div 
                    style={
                        {width: "100%", 
                        overflowX: "scroll", 
                        height:"180px", 
                        display:"flex", 
                        marginTop:12, 
                        marginBottom:12}} 
                    className={classes.scrollBar}>
                        {
                            props.hourly.map((i, index)=>{
                                return (
                                    <ItemHourly
                                        key={index + i.dt}
                                        time={i.dt * 1000}
                                        icon={i.weather[0].icon}
                                        temp={i.temp}
                                    />
                                )
                            })
                        }
                </div>

            </CardBody>
        </Card>
    )
}
export default CardHourlyInformation;