import React from 'react';
import moment from "moment";


export default function ItemHourly(props){

    const {time, icon, temp} = props;
    const GetIconWeather = (code)=>{
        return "http://openweathermap.org/img/wn/"+code+"@2x.png";
    }
    return (
        <div style={{height:150, width:100, marginLeft:"4px", marginRight:"4px"}}>
            <div style={{display:"flex", justifyContent:"center"}}>{moment(time).format("HH")}</div>
            <div style={{display:"flex", justifyContent:"center"}}>
                <img src={GetIconWeather(icon)}/>
            </div>
            <div style={{display:"flex", justifyContent:"center"}}>
                {Math.round((temp - 273.15) * 10) / 10}°C
            </div>
        </div>
    )
}