import React, { Component } from "react";
// react plugin for creating charts
import ChartistGraph from "react-chartist";
// @material-ui/core
import { withStyles } from '@material-ui/core';
import AccessTime from "@material-ui/icons/AccessTime";
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows';
import TimelineIcon from '@material-ui/icons/Timeline';
import OpacityIcon from '@material-ui/icons/Opacity';
import AcUnitIcon from '@material-ui/icons/AcUnit';
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import { dailySalesChart, emailsSubscriptionChart } from "variables/charts.js";
import dashboardStyle from "assets/jss/material-dashboard-react/views/dashboardStyle.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import NatureIcon from '@material-ui/icons/Nature';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import * as Firebase from 'firebase'
import moment from "moment";

class Dashboard extends Component {
    constructor(props) {
        super(props)
        this.state = {
            humidity: 0,
            temp: 0,
            datetime: '',
            prevDateTime: '',
            updateTempTime: 0,
            updateHumiTime: 0,
            openAlert: false,
            CO2ppm: 0,
            AirQuality: 0,
            apiMinTemp: 0,
            apiMaxTemp: 0,
            apiDateTime: '',
            locationName: "",
            weatherNowLocation: {
                locationName: "",
                temp: 0,
                temp_max: 0,
                temp_min: 0,
                humidity: 0,
                pressure: 0,
                visibility: 0,
                feels_like: 0,
                speedWind: 0,
                windDirection: 0,
                clouds: 0
            }
        }
    }

    componentDidMount() {
        Firebase.auth().onAuthStateChanged(user => {
            if (user && localStorage.firstLogin === undefined) {
                console.log(user)
                this.setState({
                    openAlert: true
                }, () => {
                    localStorage.setItem("isLogin", true);
                    localStorage.setItem("firstLogin", true);
                })
            }
            else if (user) {
                localStorage.setItem("isLogin", true);
            }
            else if (localStorage.getItem("isLogout") == "true" || localStorage.getItem("isLogout") == null) {
                this.props.history.push("/");
            }
            else if (localStorage.firstLogin === undefined) {
                this.setState({
                    openAlert: true
                }, () => {
                    localStorage.setItem("isLogin", true);
                    localStorage.setItem("firstLogin", true);
                })
            }

        });
        const rootRef = Firebase.database().ref()
        const humidityValue = rootRef.child("Humidity")
        const tempValue = rootRef.child("Temp")
        const datetime = rootRef.child("DateTime")
        const AirQuality = rootRef.child("AirQuality")
        const CO2ppm = rootRef.child("CO2ppm");
        const locationName =  rootRef.child("Location");
        humidityValue.on("value", snap => {
            let deltaSecond = this.state.datetime.substr(17, 2) - this.state.prevDateTime.substr(17, 2)
            if (deltaSecond < 0 && deltaSecond > -58) {
                deltaSecond = 60 - this.state.prevDateTime.substr(17, 2)
            }
            if (emailsSubscriptionChart.data.labels.length >= 12) {
                emailsSubscriptionChart.data.labels.splice(0, 1)
                emailsSubscriptionChart.data.series[0].splice(0, 1)
            }
            emailsSubscriptionChart.data.labels.push(this.state.datetime)
            emailsSubscriptionChart.data.series[0].push(String(snap.val()).slice(0, 5))
            this.setState({
                humidity: snap.val(),
                updateHumiTime: deltaSecond
            })
        })
        tempValue.on("value", snap => {
            let deltaSecond = this.state.datetime.substr(17, 2) - this.state.prevDateTime.substr(17, 2)
            console.log("Update time :", deltaSecond);
            console.log("temp val:", snap.val());
            if (deltaSecond < 0) {
                deltaSecond = 60 - this.state.prevDateTime.substr(17, 2)
            }
            if (dailySalesChart.data.labels.length >= 12) {
                dailySalesChart.data.labels.splice(0, 1)
                dailySalesChart.data.series[0].splice(0, 1)
            }
            dailySalesChart.data.labels.push(this.state.datetime)
            dailySalesChart.data.series[0].push(String(snap.val()).slice(0, 5))
            this.setState({
                temp: snap.val(),
                updateTempTime: deltaSecond
            })
        })
        datetime.on("value", snap => {
            this.setState({
                prevDateTime: this.state.datetime,
                datetime: snap.val()
            })
        })
        AirQuality.on("value", snap => {
            this.setState({
                AirQuality: snap.val()
            })
        })
        CO2ppm.on("value", snap => {
            this.setState({
                CO2ppm: snap.val()
            })
        });
        locationName.on("value", snap =>{
            this.setState({
                locationName: snap.val()
            })
        })
        const db = Firebase.firestore();
        fetch("http://api.openweathermap.org/data/2.5/weather?id=1580578&appid=41989156930b3d101c65e7df8215ae29")
            .then(res => res.json())
            .then(
                (result) => {
                    console.log("result api:", result);
                    let currWeather = {
                        locationName: result.name,
                        temp: result.main.temp,
                        temp_max: result.main.temp_max,
                        temp_min: result.main.temp_min,
                        humidity: result.main.humidity,
                        pressure: result.main.pressure,
                        visibility: result.visibility,
                        feels_like: result.main.visibility,
                        speedWind: result.wind.speed,
                        windDirection: result.wind.deg,
                        clouds: result.clouds.all
                    }
                    this.setState({ weatherNowLocation: currWeather });
                })
    }

    render() {
        let { classes } = this.props
        let { humidity, temp, datetime, updateHumiTime, updateTempTime,
            CO2ppm, AirQuality, apiDateTime, apiMaxTemp, apiMinTemp, locationName } = this.state
        let { weatherNowLocation } = this.state;
        return (
            <div>
                <GridContainer>
                    <GridItem xs={12}>
                        <Snackbar
                            place="tc"
                            color="success"
                            message="Đăng nhập thành công!!!"
                            open={this.state.openAlert}
                            closeNotification={() => this.setState({ openAlert: false })}
                            close
                        />
                        <Card>
                            <CardHeader color="rose" >
                                <h4 className={classes.cardTitleWhite}
                                    style={{ fontWeight: "bold" }}>
                                    <DesktopWindowsIcon
                                        className={classes.monitorIcon} />
                                    THEO DÕI NHIỆT ĐỘ, ĐỘ ẨM THỜI GIAN THỰC
                                    <DesktopWindowsIcon className={classes.monitorIcon} />
                                </h4>
                            </CardHeader>
                            <CardBody style={{ fontSize: "17px" }}>
                            <GridContainer>
                                    <GridItem xs={12} sm={6}>
                                        <div>
                                            <LocationOnIcon /> Vị trí: {locationName}
                                        </div>
                                        <div>
                                            <AccessTime />
                                            &nbsp;
                                            Ngày:
                                            &nbsp;
                                    {moment(new Date()).format("DD-MM-YYYY HH:mm:ss")}
                                        </div>
                                        <div>
                                            <OpacityIcon />
                                            &nbsp;
                                            Độ ẩm:
                                            &nbsp;
                                    {humidity} %
                                        </div>
                                        <div>
                                            <AcUnitIcon />
                                            &nbsp;
                                            Nhiệt độ:
                                            &nbsp;
                                    {temp}°C
                                        </div>
                                        <div>
                                            <WhatshotIcon />
                                            &nbsp;
                                            Nồng độ CO2 trong không khí:
                                            &nbsp;
                                    {CO2ppm === 0 ? 'None' : CO2ppm}
                                        </div>
                                    </GridItem>
                                    <GridItem xs={12} sm={6}>
                                        <div>
                                            <NatureIcon />
                                            &nbsp;
                                            Hướng gió
                                            &nbsp;
                                    {weatherNowLocation.windDirection}°
                                        </div>
                                        <div>
                                            <NatureIcon />
                                            &nbsp;
                                            Tốc độ gió
                                            &nbsp;
                                    {weatherNowLocation.speedWind}m/s
                                        </div>
                                        <div>
                                            <ArrowDownwardIcon />
                                            &nbsp;
                                            Nhiệt độ thấp nhất:
                                            &nbsp;
                                    {weatherNowLocation.temp_min - 273.15}°C
                                        </div>
                                        <div>
                                            <ArrowUpwardIcon />
                                            &nbsp;
                                            Nhiệt độ cao nhất:
                                            &nbsp;
                                    {weatherNowLocation.temp_max - 273.15}°C
                                        </div>
                                        <div>
                                            <ArrowUpwardIcon />
                                            &nbsp;
                                            Áp suất: 
                                            &nbsp;
                                    {weatherNowLocation.pressure}hPa
                                        </div>
                                    </GridItem>
                                </GridContainer>
                            </CardBody>
                        </Card>
                    </GridItem>
                </GridContainer>
                <GridContainer>
                    <GridItem xs={12}>
                        <Card>
                            <CardHeader color="warning">
                                <h4 className={classes.cardTitleWhite} style={{ fontWeight: "bold" }}>
                                    <TimelineIcon />
                                    &nbsp;
                                    BIỂU ĐỒ THAY ĐỔI NHIỆT ĐỘ, ĐỘ ẨM
                                    &nbsp;
                                    <TimelineIcon />
                                </h4>
                            </CardHeader>
                            <CardBody>
                                <div style={{padding : "0px !important"}}>
                                    <Card chart>
                                        {/* <CardHeader color="success">
                                            <ChartistGraph
                                                className="ct-chart"
                                                data={dailySalesChart.data}
                                                type="Line"
                                                options={dailySalesChart.options}
                                                listener={dailySalesChart.animation}
                                            />
                                        </CardHeader> */}
                                        {/* <div > */}
                                            <div style={{background:"#4caf50", margin:4}}>
                                                <ChartistGraph
                                                    className="ct-chart"
                                                    data={dailySalesChart.data}
                                                    type="Line"
                                                    options={dailySalesChart.options}
                                                    listener={dailySalesChart.animation}
                                                />
                                            </div>
                                        {/* </div> */}
                                        <CardBody style={{ textAlign: "center" }}>
                                            <h4 className={classes.cardTitle}>
                                                Biểu đồ nhiệt độ thời gian thực</h4>
                                            <div className={classes.stats}>
                                                <AccessTime /> updated {updateTempTime === 0 ? '...' : updateTempTime} seconds ago
                                            </div>
                                        </CardBody>
                                    </Card>
                                </div>
                                <div>
                                    <Card chart>
                                        {/* <div style={{ margin: "20px" }}></div> */}
                                        {/* <CardHeader color="info">
                                            <ChartistGraph
                                                className="ct-chart"
                                                data={emailsSubscriptionChart.data}
                                                type="Line"
                                                options={emailsSubscriptionChart.options}
                                                listener={emailsSubscriptionChart.animation}
                                            />
                                        </CardHeader> */}
                                        <div style={{background:"#00acc1", margin:4}}>
                                            <ChartistGraph
                                                className="ct-chart"
                                                data={emailsSubscriptionChart.data}
                                                type="Line"
                                                options={emailsSubscriptionChart.options}
                                                listener={emailsSubscriptionChart.animation}
                                            />
                                        </div>
                                        <CardBody style={{ textAlign: "center" }}>
                                            <h4 className={classes.cardTitle}>
                                                Biểu đồ độ ẩm thời gian thực</h4>
                                            <p className={classes.cardCategory}>
                                            </p>
                                            <div className={classes.stats}>
                                                <AccessTime /> updated {updateHumiTime === 0 ? '...' : updateHumiTime} seconds ago
                                            </div>
                                        </CardBody>
                                    </Card>
                                </div>
                            </CardBody>
                        </Card>
                    </GridItem>
                </GridContainer>
            </div >
        );
    }
}

export default withStyles(dashboardStyle)(Dashboard)
