import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';
import React from 'react';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';

function GetDateInWeek(date){
    let str = "Thứ ";
    let a = new Date(date);
    switch (a.getDay()){
        case 1: str+="hai";
        break;
        case 2: str+="ba"; 
        break;
        case 3: str+="tư";
        break;
        case 4: str+="năm";
        break;
        case 5: str+="sáu";
        break;
        case 6: str+="bảy";
        break;
        case 0: return "Chủ nhật";
        default: break;
    }
    return str;
}

export default function ItemDaily(props){
    const GetIconWeather = (code)=>{
        return "http://openweathermap.org/img/wn/"+code+"@2x.png";
    }
    const {date,
        icon,
        humidity,
        min_temp,
        max_temp
    } = props;
    return (
        <div>
            <GridContainer>
                <GridItem xs={4} style={{display:"flex", alignItems:"center"}}>
                    <div style={{display:"flex", alignItems:"center"}}>
                        {GetDateInWeek(date)}
                    </div>
                </GridItem>
                <GridItem xs style={{display:"flex", alignItems:"center"}}>
                    <div style={{display:"flex", alignItems:"center"}}>
                        <img src={GetIconWeather(icon)} width="100%" height="auto"/>
                    </div>
                </GridItem>
                {/* <GridItem xs style={{display:"flex", alignItems:"center"}}>
                    <div style={{display:"flex", alignItems:"center"}}>
                        {humidity}%
                    </div>
                </GridItem> */}
                <GridItem xs style={{display:"flex", alignItems:"center"}}>
                    <div style={{display:"flex", alignItems:"center"}}>
                        {Math.round((min_temp -273.15) * 10) / 10}°C
                    </div>
                </GridItem>
                <GridItem xs style={{display:"flex", alignItems:"center"}}>
                    <div style={{display:"flex", alignItems:"center"}}>
                        {Math.round((max_temp -273.15) * 10) / 10}°C
                    </div>
                </GridItem>
            </GridContainer>
        </div>
    )
}