import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import HelpIcon from '@material-ui/icons/Help';
import ShoppingBasket from '@material-ui/icons/ShoppingBasket';
import ThumbDown from '@material-ui/icons/ThumbDown';
import ThumbUp from '@material-ui/icons/ThumbUp';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import HomeIcon from '@material-ui/icons/Home';
import WbSunnyIcon from '@material-ui/icons/WbSunny';
import { withRouter } from "react-router-dom";
import { Paper } from '@material-ui/core';

class TabFooter extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            value : 0
        }
    }
    activeRoute(routeName) {
        return window.location.href.indexOf(routeName) > -1 ? true : false;
    }
    a11yProps(index) {
        return {
          id: `scrollable-prevent-tab-${index}`,
          'aria-controls': `scrollable-prevent-tabpanel-${index}`,
        };
    }

    setValue(newValue){
        this.setState({value : newValue});
    }

    handleChange = (event, newValue) => {
        this.setValue(newValue);
    };
    hanleOnClick = (layout, path)=>{
        this.props.history.push(layout+path);
    }
    render(){
        const {routes} = this.props;
        return (
            <div>
                <Paper position="static" style={{
                flexGrow: 1,
                width: '100%',
                backgroundColor: "#00acc1",
                display:"flex",
                justifyContent:"space-around"
                }}>
                    <Tabs
                    value={this.state.value}
                    onChange={this.handleChange}
                    variant="scrollable"
                    scrollButtons="off"
                    >
                        {
                            routes.map((i, index)=>{
                                return (
                                    <Tab 
                                        key={index} 
                                        icon={<i.icon />} 
                                        onClick={()=> this.hanleOnClick(i.layout,i.path)} 
                                        {...this.a11yProps(index)} />
                                )
                            })
                        }
                    </Tabs>
                </Paper >
            </div>
        )
    }
}
export default withRouter(TabFooter);