import { makeStyles, TextField } from "@material-ui/core";
import { Autocomplete, createFilterOptions } from "@material-ui/lab";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem";
import helper from "Helper/helper";
import React, { useEffect } from "react";
import CardHourlyInformation from "./CardHourlyInformation";
import ListCities from './ListOfCities';
import ItemDaily from './ItemDaily';
import CardHeader from "components/Card/CardHeader";
import CardBody from "components/Card/CardBody";
import Card from "components/Card/Card";


const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: 4,
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: 4,
  },
}));

export default function Maps() {
  const classes = useStyles();
  const [city, setCity] = React.useState(ListCities[0]);
  const [weather, setWeather] = React.useState({
    date: (new Date()).getTime(),
    iconCode: "",
    temp: 0,
    speedWind: 0
  });

  const [weekly, setWeekly] = React.useState([]);

  const [hourly, setHourly] = React.useState([]);

  const handleChange = (event, newValue) => {
    console.log("newValue:", newValue);
    setCity(newValue);
    // document.getElementById("outfocus").target.focus();
    document.getElementById("outfocus").focus();
  };
  useEffect(() => {
    getDataWeather(city);
  }, [city]);
  const getDataWeather = (location) => {
    if (location == null) return;
    let lat = location.coord.lat;
    let lon = location.coord.lon;
    fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${lat}&lon=${lon}&exclude=minutely,current,alerts&appid=${helper.apiKey}&lang=vi`)
      .then(res => res.json())
      .then(
        (result) => {
          console.log("api data:", result);
          if (result) {
            let value = result.daily[0];
            let w = {
              date: value.dt * 1000,
              iconCode: value.weather[0].icon,
              temp: value.temp.eve,
              speedWind: value.wind_speed,
              clouds: value.clouds,
              humidity: value.humidity,
              sunrise: value.sunrise * 1000,
              sunset: value.sunset * 1000,
              pressure: value.pressure,
              max_temp: value.temp.max,
              min_temp: value.temp.min,
              description: value.weather[0].description
            }
            setWeather(w);
            let d = result.daily.map((i, index) => {
              return {
                date: i.dt * 1000,
                icon: i.weather[0].icon,
                humidity: i.humidity,
                min_temp: i.temp.min,
                max_temp: i.temp.max
              }
            })
            setWeekly(d);
            setHourly(result.hourly);

          }
        }
      )
  }
  let filterOption = createFilterOptions({
    stringify: option => option.name,
    ignoreAccents: true,
    ignoreCase: true
  });
  return (
    <div>
      <GridContainer>
        <GridItem sm={6} xs={12}>
          <input type="button" style={{ position: "fixed", left: "-100vw", top: "-100vh", width: 0, height: 0, overflow: "hidden" }} id="outfocus" />
          <Autocomplete
            id="combo-box-demo"
            options={ListCities}
            getOptionLabel={(option) => option.name}
            filterOptions={filterOption}
            defaultValue={ListCities[0]}
            onChange={handleChange}
            renderInput={(params) => <TextField {...params} label="Chọn khu vực" variant="outlined" />}
          />
        </GridItem>
      </GridContainer>
      <GridContainer>
        <GridItem sm={12} xs={12}>
          <CardHourlyInformation
            {...weather}
            hourly={hourly}
            weekly={weekly}
          />
        </GridItem>
      </GridContainer>
      <Card>
        <CardHeader color="success">
          {/* <GridContainer>
            <GridItem xs style={{display:"flex", alignItems:"center", fontWeight:"bold"}}>
                Thứ
            </GridItem>
            <GridItem xs style={{display:"flex", alignItems:"center", fontWeight:"bold"}}>
                Trạng thái
            </GridItem>
            <GridItem xs style={{display:"flex", alignItems:"center", fontWeight:"bold"}}>
                Nhiệt độ cao nhất
            </GridItem>
            <GridItem xs style={{display:"flex", alignItems:"center", fontWeight:"bold"}}>
                Nhiệt độ thấp nhất
            </GridItem>
          </GridContainer> */}
          {
            weekly.map((i, index) => {
              return (
                <div key={index}>
                  <ItemDaily
                    {...i}
                  />
                </div>
              );
            })
          }
        </CardHeader>
        <CardBody>
          <div style={{ display: "flex", justifyContent: "center" }}>
            THỜI TIẾT TRONG 7 NGÀY TỚI
          </div>
        </CardBody>
      </Card>
      <GridContainer>
        <GridItem xs={12}>

        </GridItem>
      </GridContainer>
    </div>
  );
}
